import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { ChartComponent } from 'ng-apexcharts';

@Component({
  selector: 'app-oer-keyword-trend',
  templateUrl: './oer-keyword-trend.component.html',
  styleUrls: ['./oer-keyword-trend.component.scss'],
})
export class OerKeywordTrendComponent implements OnInit {
  @ViewChild("oer-keyword-trend") chart: ChartComponent;
  @Input() name: string;
  @Input() yearMap: Map<string, number>;

  chartOptions = {
    series: [
      {
        name: 'Desktops',
        data: [],
      },
    ],
    chart: {
      height: 150,
      type: 'line',
      zoom: {
        enabled: false,
      },
    },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      curve: 'straight',
    },
    title: {
      text: '',
      align: 'left',
    },
    grid: {
      row: {
        colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
        opacity: 0.5,
      },
    },
    xaxis: {
      categories: [],
    },
  };

  constructor() {}

  ngOnInit(): void {
    if (this.yearMap) {
      let data = [];
      for (const e of Object.entries(this.yearMap)) {
        data.push({
          year: e[0],
          count: e[1]
        });
      }

      data = data.sort((a, b) => {
        if (a.year < b.year)
          return -1;
        if (a.year > b.year)
          return 1;
        return 0;
      });

      for (const d of data) {
        this.chartOptions.series[0].data.push(d.count);
        this.chartOptions.xaxis.categories.push(d.year);
      }
    }

    if (this.name) {
      this.chartOptions.series[0].name = this.name;
    }
  }
}
