import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartComponent } from 'ng-apexcharts';
import { DataService } from 'src/app/data.service';

@Component({
  selector: 'app-oer-year-dist',
  templateUrl: './oer-year-dist.component.html',
  styleUrls: ['./oer-year-dist.component.scss'],
})
export class OerYearDistComponent implements OnInit {
  @ViewChild('oer-year-dist-chart') chart: ChartComponent;
  loaded = false;

  chartOptions = {
    series: [],

    xaxis: {
      categories: [],
    },

    chart: {
      type: 'bar',
      width: '100%',
      height: 'auto',
    },
  };

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.loaded.subscribe(() => {
      const years = this.dataService.years;
      const name = 'OERs by Year';
      const values = [];

      for (const e of years) {
        this.chartOptions.xaxis.categories.push(e.year);
        values.push(e.count);
      }

      this.chartOptions.series.push({
        name,
        data: values,
      });

      this.loaded = true;
    });
  }
}
