import { HttpClient } from '@angular/common/http';
import { EventEmitter, Injectable } from '@angular/core';
import { ReplaySubject } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  path = 'https://resourcedashboard.herokuapp.com' + '/data'; //deployment
  data = [];
  dataMap: any[] = [];
  authors = [];
  languages = [];
  locations: any[] = [];
  years = [];
  licenses = [];
  loaded = new ReplaySubject();
  downloading = new EventEmitter<number>();
  processing = new EventEmitter<number>();

  constructor(private http: HttpClient) { }

  fetchData() {
    this.downloading.emit(-1);

    this.http
      .get<any[]>(this.path, {
        headers: {
          'auth-token': environment.token,
        },
      })
      .pipe(
        tap((db_data) => {
          this.downloading.emit(db_data.length);
        }),

        map((db_data: any) => {
          let processed = 0;
          this.dataMap = db_data["dmap"];
          this.authors = db_data["aut"];
          console.log(this.authors);
          this.languages = db_data["lang"];
          this.locations = db_data["loca"];
          this.years = db_data["year"];
          this.licenses = db_data["licenses"];
          return db_data;
        })
      )
      .subscribe((db_data) => {
        this.loaded.next(true);
      });
  }

  //getData() {
    //return this.data;
 // }

  getTopKeywords(count: number = -1) {
    if (count == -1) count = this.dataMap.length;
    return this.dataMap.slice(0, count);
  }

  getTop20Authors() {
    return this.authors.slice(0, 20);
  }
}
