import { Component, OnInit, ViewChild } from '@angular/core';
import { DataService } from 'src/app/data.service';

import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import * as am4plugins_wordCloud from '@amcharts/amcharts4/plugins/wordCloud';

@Component({
  selector: 'app-oer-keyword-dist',
  templateUrl: './oer-keyword-dist.component.html',
  styleUrls: ['./oer-keyword-dist.component.scss'],
})
export class OerKeywordDistComponent implements OnInit {
  @ViewChild('oer-keyword-dist-chart') chartElement: any;

  chart = null;
  loaded = false;
  text = [];

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.loaded.subscribe(() => {
      const data = this.dataService.getTopKeywords(70);

      const htmlElement = document.getElementById(
        'oer-keyword-dist-chart'
      ) as HTMLCanvasElement;

      if (htmlElement) {
        this.createChart(data);
      }

      this.loaded = true;
    });
  }

  private createChart(keywords: any[]) {
    /* Chart code */
    // Themes begin
    //am4core.useTheme(am4themes_animated);
    // Themes end

    let chart = am4core.create(
      'oer-keyword-dist-chart',
      am4plugins_wordCloud.WordCloud
    );
    let series = chart.series.push(new am4plugins_wordCloud.WordCloudSeries());

    series.accuracy = 3;
	series.randomness = 0;
    series.rotationThreshold = 0.7;
    series.maxCount = 70;
    series.minWordLength = 2;
	series.minFontSize = 10; 
    series.labels.template.tooltipText = '{word}: {value}';
    series.fontFamily = 'Courier New';
    series.maxFontSize = am4core.percent(20);

    series.dataFields.word = "name";
    series.dataFields.value = "value";
    series.data = keywords;
	series.heatRules.push({
 "target": series.labels.template,
 "property": "fill",
 "min": am4core.color("#4242ff"),
 "max": am4core.color("#000042"),
 "dataField": "value"
});
	}
  
}
