import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/data.service';
import { MapItem } from '../../MapItem';

@Component({
  selector: 'app-plain',
  templateUrl: './plain.component.html',
  styleUrls: ['./plain.component.scss'],
})
export class PlainComponent implements OnInit {
  total = 0;
  locations = [];
  loaded = false;

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.loaded.subscribe(() => {
      const locations = this.dataService.locations;
      this.total = 0;

      for (const location of locations) {
        this.locations.push({
          ...location,
        });
        this.total += location.count;
      }

      this.loaded = true;
    });
  }
}


