import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartComponent } from 'ng-apexcharts';
import { DataService } from 'src/app/data.service';
import { MapItem } from 'src/app/MapItem';

@Component({
  selector: 'app-oer-lang-dist',
  templateUrl: './oer-lang-dist.component.html',
  styleUrls: ['./oer-lang-dist.component.scss'],
})
export class OerLangDistComponent implements OnInit {
  @ViewChild('oer-lang-dist-chart') chart: ChartComponent;
  loaded = false;

  chartOptions = {
    series: [],
    labels: [],
    chart: {
      type: 'pie',
      width: '100%',
      height: 'auto',
    },

    legend: {
      position: 'bottom',
    },
  };

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    this.dataService.loaded.subscribe(() => {
      const langs = this.dataService.languages;

      for (const lang of langs) {
        this.chartOptions.labels.push(lang.name);
        this.chartOptions.series.push(lang.count);
      }

      this.loaded = true;
    });
  }
}
